* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  background-color: #f5f5dc; /* Fondo color crema */
}

/* Menu superior */
#navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #333; /* Fondo gris oscuro de la barra de menú */
  color: white;
  padding: 10px 20px;
  z-index: 10;
}

#navbar .nav-content {
  display: flex;
  align-items: center;  /* Alinea verticalmente el logo y el título */
  justify-content: flex-start;  /* Alinea todo el contenido a la izquierda */
  width: 100%; /* Asegura que los elementos se estiren a toda la barra */
}

#navbar #nav-logo {
  max-width: 40px; /* Ajusta el tamaño del logo */
  margin-right: 10px; /* Añade un pequeño espacio entre el logo y el título */
}

#navbar #nav-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

#navbar #nav-title a {
  color: white; /* Asegura que el enlace tenga el mismo color que el texto */
  text-decoration: none;
}

#navbar ul {
  list-style: none;
  display: flex;
  margin-left: auto; /* Esto asegura que los botones estén alineados a la derecha */
}

#navbar ul li {
  margin-left: 20px; /* Espacio entre los botones del menú */
}

#navbar ul li a {
  text-decoration: none;
  color: white;
  font-weight: 500;
  font-size: 1rem;
}

#navbar ul li a:hover {
  text-decoration: underline;
}


/* Text de presentació.html */
.content-column {
  max-width: 800px;
  margin: 100px auto;  
  padding: 20px;
}

.content-column h2 {
  text-align: center;
  margin-bottom: 20px;
}

.content-column h3 {
  margin-top: 30px;
  font-size: 1.2rem;
  font-weight: 600;
}

.content-column p {
  line-height: 1.6;
  font-size: 1.1rem;
  margin-bottom: 20px;
}

/* Imágenes flotantes en presentació.html */
.floated-images {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.floated-img {
  width: 48%;
  border-radius: 8px;
}

.floated-img.left {
  float: left;
  margin-right: 20px;
}

.floated-img.right {
  float: right;
  margin-left: 20px;
}

/* Estilos de la página principal (index.html) */
.hero-section {
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-top: 150px;
  width: 1200px; 
}

.hero-image-img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* La imagen cubrirá toda la pantalla sin distorsionarse */
}

.hero-title {
  position: absolute;
  bottom: -180px; /* El título se coloca justo debajo de la imagen */
  left: 50%;
  transform: translateX(-50%);
  color: white;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5); /* Fondo oscuro semitransparente para mejorar la legibilidad */
  padding: 10px;
}

/* Añadir más espacio en el contenido para permitir scroll */
main {
  padding: 20px;
}

section {
  margin-bottom: 40px;
}

/* Estilo para el contenido de index.html */
section h2 {
  margin-top: 50px;
  font-size: 1.5rem;
  font-weight: 600;
}

section p {
  font-size: 1rem;
  line-height: 1.6;
  margin-top: 20px;
}

/* Responsividad para móviles */
@media (max-width: 768px) {
  /* Ajustes para la columna de texto de presentació.html */
  .content-column {
    width: 90%; /* Aumenta el ancho del contenedor */
  }

  .floated-images {
    flex-direction: column;
  }

  .floated-img {
    width: 100%;
    margin: 10px 0; /* Agrega margen entre las imágenes */
  }

  /* Ajustes para la imagen en index.html */
  .hero-section {
    height: auto;
  }

  .hero-image-img {
    width: 480px;
    height: auto;
    margin-top: 135px;
  }

  .hero-title {
    font-size: 2rem;
    padding: 30px;
    position: relative;
    top: 0px;
  }

  #navbar ul {
    display: block;
    text-align: center;
  }

  #navbar ul li {
    margin: 15px 0;
  }
}

/* Estils recorrent.html */

/* Reseteo de márgenes y padding por defecto */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Configuración general del cuerpo de la página */
body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.6;
  background-color: #f5f5dc; /* Fondo de la página color crema */
}

/* Barra de navegación fija a la parte superior */
#navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #333; /* Barra de menú gris oscuro */
  color: white;
  padding: 10px 20px;
  z-index: 10;
}

#navbar .nav-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#navbar #nav-logo {
  max-width: 40px;
}

#navbar #nav-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
}

#navbar ul {
  list-style: none;
  display: flex;
}

#navbar ul li {
  margin: 0 15px;
}

#navbar ul li a {
  text-decoration: none;
  color: white;
  font-weight: 500;
}

#navbar ul li a:hover {
  text-decoration: underline;
}

/* Contenido de la página */
.content {
  margin-top: 100px; /* Espacio para la barra de navegación */
  padding: 20px;
}

.content h2 {
  font-size: 2rem;
  margin-bottom: 20px;
  margin-bottom: 20px;
  text-align: center;
}

.content p {
  font-size: 1rem;
  margin-bottom: 40px;
}

/* Botones con imagen y faldón */
.botons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.botons .boton {
  position: relative;
  width: 250px;
  margin: 15px;
  text-align: center;
}

.botons .boton img {
  width: 100%;
  height: auto;
  display: block;
}

.botons .boton .faldon {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #333;
  color: white;
  padding: 10px 0;
  font-weight: 700;
  text-transform: uppercase;
}

/* Foto del mapa en la parte inferior */
#mapa {
  width: 100%;
  margin-top: 40px;
  margin-bottom: 40px;
}

/* Media Queries para pantallas pequeñas */
@media screen and (max-width: 768px) {
  .botons .boton {
    width: 100%; /* Hacer los botones de ancho completo en móviles */
    margin-bottom: 15px;
  }

  .botons .boton .faldon {
    font-size: 1rem; /* Reducir tamaño de fuente en móviles */
  }

  #mapa {
    width: 100%; /* Asegurarse que la imagen del mapa ocupe todo el ancho */
  }

  section p {
    font-size: 1rem;
    margin-left: 20px;
    margin-right: 20px;
  }
}


/* Imagen final - Mapa */
.mapa {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.mapa-img {
  width: 100%;
  max-width: 800px; /* Limitar el ancho máximo de la imagen */
  height: auto;
}

/* Estilos para la versión móvil */
@media screen and (max-width: 768px) {
  .buttons-container {
    flex-direction: column;
    align-items: center;
  }

  .button {
    width: 80%;
    margin-bottom: 20px;
  }

  .mapa-img {
    max-width: 100%;
  }
}
